import React from 'react';
import {List, ListItem, ListItemButton, ListItemText} from '@mui/material';

const navItems = [
    {
        id: "whoAmITop",
        label:"Qui suis-je?",
        url: "/"
    },
    {
        id: "myCVTop",
        label:"Mon CV",
        url: "/cv"
    },
    {
        id: "projectsWorkedOnTop",
        label:"Mes projets raliss",
        url: "/projects"
    }
]

const flexContainer = {
    display: 'flex',
    flexDirection: 'row',
    innerWidth: "70%",
    marginLeft: "auto",
    marginRight: "-16",
};

interface PagesChoiceProps{
    currentPage: String
};

class PagesChoice extends React.Component<PagesChoiceProps, {}>{
    render(): React.ReactNode {
        const props = this.props;
        return(
            <List style={flexContainer}>
                {navItems.map((item) => {
                    if(item.id == props.currentPage){
                        return(
                            <ListItem key={item.id} disablePadding>
                                <ListItemButton
                                    sx={{
                                        textAlign: 'center',
                                        color: "#224F1E"
                                    }}
                                    disabled
                                >
                                    <ListItemText primary={item.label} />
                                </ListItemButton>
                            </ListItem>
                        )
                    }else{
                        return(
                            <ListItem key={item.id} disablePadding>
                                <ListItemButton sx={{
                                    textAlign: 'center',
                                    color: "#212121"
                                    }}
                                    href={item.url}
                                    >
                                    <ListItemText primary={item.label} />
                                </ListItemButton>
                            </ListItem>
                        )
                    }
                })}
            </List>
        )
    }
}

export default PagesChoice;