import React from 'react';
import {MenuItem, Menu} from '@mui/material';

const languages = [
    {
        id:"french",
        label:"Franais",
        beta:false,
    },
    {
        id:"english",
        label:"English",
        beta:false,
    },
    {
        id:"japanese",
        label:"",
        beta:true
    }
]

export default function MenuList(){

    const [anchorEl, setAnchorEl] = React.useState(null);
    
    const [mobileMoreAnchorEl, setMobileMoreAnchorEl] =
    React.useState(null);
    
    const isMenuOpen = Boolean(anchorEl);
    
    const handleMenuClose = () => {
        setAnchorEl(null);
        handleMobileMenuClose();
    };
    
    const handleMobileMenuClose = () => {
        setMobileMoreAnchorEl(null);
    };

    return(
        <Menu
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            id={"portfolio-language-setup"}
            keepMounted
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
            open={isMenuOpen}
            onClose={handleMenuClose}
        >
        {languages.map((singleLanguage, i) => {
            if(singleLanguage.beta){
                return (
                <MenuItem
                    id={singleLanguage.id}
                    key={singleLanguage.id}
                >
                    {singleLanguage.label}
                </MenuItem>) 
            }else{
                return (
                <MenuItem
                id={singleLanguage.id}
                key={singleLanguage.id}
                disabled
                >
                {singleLanguage.label}
                </MenuItem>
                ) 
            }
        })}
        </Menu>
    );
}